.contact-us__textarea {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 19px;
  color: #ffffff;
  background-color: #0B7572;
  width: 290px;
  margin-top: 48px;
  border-color: rgba(255, 255, 255, 0.2);
  resize: none;
  margin-bottom: 66px;
  padding: 10px;
}
.contact-us__wrap {
  margin-bottom: 30px;
  position: relative;
  z-index: 0;
}
.contact-us__input {
  border: none;
  width: 312px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  background-color: #0B7572;
  padding: 16px 0;
  z-index: -1;
}
.contact-us__input::placeholder {
  color: #ffffff;
  font-size: 15px;
  line-height: 19px;
  padding-left: 30px;
}
.contact-us__label--task::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 0;
  width: 21px;
  height: 19px;
  background-image: url("../img/icon-clip.svg");
  background-repeat: no-repeat;
  z-index: 3;
  box-sizing: border-box;
}
.contact-us__label--image::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 0;
  width: 21px;
  height: 19px;
  background-image: url("../img/icon-add.svg");
  background-repeat: no-repeat;
  z-index: 5;
  box-sizing: border-box;
}
.contact-us__button {
  width: 313px;
  height: 48px;
  background-color: #ffffff;
  color: #0B7572;
  font-family: "Exo 2", "Arial", sans-serif;
  font-size: 15px;
  line-height: 23px;
  font-weight: 600;
  border: none;
  border-radius: 4px;
  margin-top: 50px;
}
.contact-us__button:hover {
  color: #ffffff;
  background-color: #9F9F9F;
}
.contact-us__button:active {
  color: rgba(255, 255, 255, 0.2);
  background-color: #9F9F9F;
}
.data {
  padding: 64px 0 20px 0;
  width: 343px;
}
.data__title {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 28px;
  line-height: 35px;
  color: #363738;
  margin: 0;
}
.data__lead,
.data__inner-title {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  line-height: 24px;
  margin-top: 30px;
}
.data__desc,
.data__link {
  font-size: 15px;
  line-height: 23px;
  color: #363738;
}
.data__desc {
  margin: 0;
}
.data__link {
  position: relative;
  margin-left: 30px;
}
.data__link::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -30px;
  width: 20px;
  height: 20px;
  background-image: url("../img/icon-file.svg");
  background-repeat: no-repeat;
}
.data__inner {
  margin-top: 40px;
}
.data__inner-title {
  margin-top: 0;
}
.data__image-list {
  list-style: none;
  padding: 0;
  display: flex;
}
.data__image {
  width: 109px;
  height: 86px;
}
.data__image-link {
  color: #363738;
  text-align: center;
  margin-top: 12px;
}
.data__button {
  display: none;
}
@media (min-width: 768px) {
  .data__button {
    width: 313px;
    height: 48px;
    background-color: #0B7572;
    color: #ffffff;
    font-family: "Exo 2", "Arial", sans-serif;
    font-size: 15px;
    line-height: 23px;
    font-weight: 600;
    border: none;
    border-radius: 4px;
    margin-top: 50px;
    text-align: center;
    display: block;
  }
  .data__button:hover {
    color: #0B7572;
    background-color: #ffffff;
  }
  .data__button:active {
    color: rgba(255, 255, 255, 0.2);
    background-color: #9F9F9F;
  }
  .data {
    width: auto;
  }
}
.about-work {
  padding: 50px 0 20px 0;
  width: 343px;
}
.about-work__title {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 28px;
  line-height: 35px;
  color: #363738;
  margin: 0;
}
.about-work__slogan {
  font-size: 15px;
  line-height: 23px;
  color: #363738;
}
@media (min-width: 768px) {
  .about-work {
    width: auto;
  }
}
